header {
    display: flex;
    align-items: center;
    justify-content:space-between;
    padding: 10px;
    height: auto;
    background-color: black; 
}

.user {
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items:flex-end;    
}

.user p {
    margin: 5px;
}

.user button {
    background-color: transparent;
    border: 1px solid aliceblue;
    padding: 5px;
    margin: 5px;
}

.user-name {
    font-size: larger;
}