.list-pokemons {
    background-color: rgb(61, 61, 61);
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.list-pokemons button {
    background-color: transparent;
    padding: 5px;
    width: 100px;
    margin: 10px;
    border-radius: 0.5rem;
    cursor: pointer;
}
