
.pokemon {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    text-align: center;
    height: 10rem;
    width: 10rem;
    box-shadow: #0000003d 0px 3px 8px;
    margin: 2rem;    
}

.pokemon img{
    height: 6rem;
    width: 6rem;
}

.grow {   
    animation: createBox 1s;
    width: 98px;
    height: 98px;
  }
  @keyframes createBox {
    from {
      transform: scale(0);
    }
    to {
      transform: scale(1);
    }
  }